        font-size: 1rem;
        font-weight: 500;
        justify-content: center;
        line-height: 1.75rem;
        padding: .75rem 1.65rem;
        position: relative;
        text-align: center;
        text-decoration: none #000000 solid;
        text-decoration-thickness: auto;
        width: 50%;
        /* max-width: 35vw; */
        position: relative;
        cursor: pointer;
        transform: rotate(-2deg);
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        padding-bottom: 1em;
        margin-top: 1em;
        }
        
        .download-btn:focus {
        outline: 0;
